@media (min-width: 992px) {
    .rule_guide {
        border-left: 1px solid #e1e3ea;
        border-left: 1px solid var(--bs-border-color-translucent);
        width: 480px !important;
        min-width: 480px;
        height: calc(100vh - 160px);
    }

    .ruleList_outer{
        height: calc(100vh - 200px)
    }

    .rule_action{
        height: calc(100vh - 200px);
    }
    
}

.rule-box:after{
    position: absolute;
    content: "";
    // border: 1px dashed #4E69ED;
    border: 1px dashed #4179ff;
    left: 11px;
    top: 15px;
    width: 1px;
    height: 100%;
}

[data-product="taggbox"] .rule-box:after{
    border: 1px dashed #ff4051;
 }

.rule-box:last-child:after{
    display: none;
}

.prefix_rule:hover{
    border-color:var(--bs-primary) !important;
}

.add_rule {
    margin-bottom: 38px;
}

.add_more_rule {
    margin-bottom: 25px;
    margin-top: 15px;
}

.add_more_rule:after, 
.add_more_rule:before, 
.add_rule:before {
    background-color: #e4e6ef;
    background-color: var(--bs-gray-300);
    content: "";
    height: 25px;
    left: 0;
    margin: 0 auto;
    position: absolute;
    right: 0;
    top: 100%;
    width: 1px;
}

.add_rule:after {
    bottom: -42px;
    color: #e4e6ef;
    color: var(--bs-gray-300);
    content: "\f107";
    font-family: "Font Awesome 6 Pro";
    font-size: 16px;
    left: 0;
    margin: 0 auto;
    position: absolute;
    right: 0;
    text-align: center;
}

.add_rule:before {
    height: 30px;
}

.add_more_rule:before {
    bottom: 100%;
    height: 15px;
    top: auto;
}

.add_more_rule .dropdown-menu, .more_actions {
    left: calc(-318px / 2 + 12px) !important;
}

.action_gap {
    background-color: #e4e6ef;
    background-color: var(--bs-gray-300);
    height: 10px;
    left: 0;
    margin: 0 auto;
    right: 0;
    width: 1px;
}

.rule_actions .dropdown-item:hover i{
    color: var(--text-primary) !important;
}


